import maxtx from "../assets/img/treasury.png";
import marketing from "../assets/img/marketing2.png";
import "react-multi-carousel/lib/styles.css";
import colorSharp from "../assets/img/color-sharp.png";

import Carousel from "react-multi-carousel";
/*1.5 BNB Starting LP. 10,000 Total Supply. 
2% Max Wallet. 1% Max Tx. Low 5% Tax. 3% BUSD refunds & 2% Marketing
*/

export const Launchappskills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 2,
      slidesToSlide: 1,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 2,
      slidesToSlide: 1,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 1,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  return (
    <section className="Launchapp skill" id="skills">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="skill-bx wow zoomIn">
              <Carousel
                responsive={responsive}
                infinite={false}
                removeArrowOnDeviceType={["superLargeDesktop", "desktop"]}
                className="owl-carousel owl-theme skill-slider"
              >
                <div className="item">
                  <img src={maxtx} alt="Image" />
                  <h5>
                    Treasury Wallet Ca ~<br></br>
                    0x996695879eFab959d1D77
                    <br />
                    3D5B60752F824F6fba8{" "}
                  </h5>
                  <a
                    href="https://bscscan.com/address/0x996695879eFab959d1D773D5B60752F824F6fba8"
                    target={"_blank"}
                  >
                    {" "}
                    <button className="launch-text">
                      <span>Go TO Wallet</span>
                    </button>
                  </a>
                </div>
                <div className="item">
                  <img src={marketing} alt="Image" />
                  <h5>
                    Marketing Wallet Ca ~ <br />
                    0xecBfF2dEe3f6b204623C1e
                    <br />
                    7F68f32378A567ad32
                  </h5>

                  <a
                    href="https://bscscan.com/address/0xecBfF2dEe3f6b204623C1e7F68f32378A567ad32"
                    target={"_blank"}
                  >
                    {" "}
                    <button className="launch-text">
                      <span>Go TO Wallet</span>
                    </button>
                  </a>
                </div>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
      <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  );
};
