import "animate.css";
import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/walletsraf.png";
import transfer from "../assets/img/E-Wallet.svg";
import bitcoin from "../assets/img/creative.png";
import fail from "../assets/img/freedom.svg";
import TrackVisibility from "react-on-screen";
import { Launchappskills } from "./launchappskills";

export const Launch = () => {
  return (
    <section className="launchapp">
      <h1 className="text2"> COMING SOON</h1>
      <p className="text2">
        A Treasury System for Cryptocurrencies Enabling Better Collaborative
        Intelligence
      </p>
      <section className="contact defi ">
        <Container>
          <h4>
            {" "}
            Even though decentralised finance is still in the early stages of
            development as an alternative to the traditional finance system, a
            number of apps have already been developed. The apps are giving
            people a taste of what the financial future could look like.
          </h4>
          <Row className="align-items-center defi-rows">
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <img
                    className={
                      isVisible ? "animate__animated animate__zoomIn" : ""
                    }
                    src={contactImg}
                    alt="Contact Us"
                  />
                )}
              </TrackVisibility>
            </Col>
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div
                    className={
                      isVisible ? "animate__animated animate__fadeIn" : ""
                    }
                  >
                    <h2> 1. Baking services </h2>
                    <div className="defi-home">
                      <p>
                        Decentralised finance is an emerging industry that
                        promises to revolutionise the traditional finance
                        sector. The need for an open, transparent, and secure
                        financial system is the key driver behind the
                        decentralised finance vs. traditional finance debate, so
                        it does not come as a surprise that decentralised
                        finance is slowly emerging as an alternative to today’s
                        financial system.
                      </p>
                    </div>
                  </div>
                )}
              </TrackVisibility>
            </Col>
          </Row>

          <Row className="align-items-center defi-rows">
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div
                    className={
                      isVisible ? "animate__animated animate__fadeIn" : ""
                    }
                  >
                    <h2>2. Addressing global financial issues</h2>
                    <div className="defi-home">
                      <p>
                        Thanks to the integration of digital ledger technologies
                        in applications, people in remote parts of the world can
                        now access banking services through their mobile
                        devices. For that reason, DeFi promises to succeed in
                        areas where traditional finance has failed.
                      </p>
                    </div>
                  </div>
                )}
              </TrackVisibility>
            </Col>

            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <img
                    className={
                      isVisible ? "animate__animated animate__zoomIn" : ""
                    }
                    src={transfer}
                    alt="Contact Us"
                  />
                )}
              </TrackVisibility>
            </Col>
          </Row>

          <Row className="align-items-center defi-rows">
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <img
                    className={
                      isVisible ? "animate__animated animate__zoomIn" : ""
                    }
                    src={fail}
                    alt="Contact Us"
                  />
                )}
              </TrackVisibility>
            </Col>
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div
                    className={
                      isVisible ? "animate__animated animate__fadeIn" : ""
                    }
                  >
                    <h2>3. Circumventing Censorship and Restrictions</h2>
                    <div className="defi-home">
                      <p>
                        Following the 2008 Financial Crisis, many people lost
                        their fortunes as a good number of banks went under.
                        Concerned by the threat posed by the current global
                        financial systems, many people are looking to emerging
                        technologies to shield themselves. Decentralised finance
                        is also proving to be a reliable method of circumventing
                        issues related to hyperinflation resulting from currency
                        manipulation or unexpected devaluations, as is the case
                        in China.
                      </p>
                    </div>
                  </div>
                )}
              </TrackVisibility>
            </Col>
          </Row>

          <Row className="align-items-center defi-rows">
            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div
                    className={
                      isVisible ? "animate__animated animate__fadeIn" : ""
                    }
                  >
                    <h2>4. Financial Creativity</h2>
                    <div className="defi-home">
                      <p>
                        Decentralised finance is also proving to be a reliable
                        tool for enhancing the development of financial products
                        that in the past were the domain of large, licensed
                        institutions
                      </p>
                    </div>
                  </div>
                )}
              </TrackVisibility>
            </Col>

            <Col size={12} md={6}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <img
                    className={
                      isVisible ? "animate__animated animate__zoomIn" : ""
                    }
                    src={bitcoin}
                    alt="Contact Us"
                  />
                )}
              </TrackVisibility>
            </Col>
          </Row>
        </Container>
      </section>

      <Launchappskills />
    </section>
  );
};

/*
  1. Baking services 
Thanks to the integration of digital ledger technologies in applications, people in remote parts of the world can now access banking services through their mobile devices. For that reason, DeFi promises to succeed in areas where traditional finance has failed.
2. Addressing global financial issues
Following the 2008 Financial Crisis, many people lost their fortunes as a good number of banks went under. Concerned by the threat posed by the current global financial systems, many people are looking to emerging technologies to shield themselves.
Decentralised finance is also proving to be a reliable method of circumventing issues related to hyperinflation resulting from currency manipulation or unexpected devaluations, as is the case in China.
3. Circumventing Censorship and Restrictions
The idea of building censorship-resistant products in the financial sector will continue to fuel decentralised finance popularity.
4. Financial Creativity
Decentralised finance is also proving to be a reliable tool for enhancing the development of financial products that in the past were the domain of large, licensed institutions
  */
