import { Col, Row } from "react-bootstrap";
import tweet1 from "../assets/img/tweet1.jpg"
import tweet2 from "../assets/img/tweet2.jpeg"
import kucoin from "../assets/img/kucoin.png";
import { ArrowRightCircle } from 'react-bootstrap-icons';
export const Media = ({}) => {
  return (
    <Col>
      <div className="Media">
        <Row>
          <Col className="Media-links">

          <div className="Media-news">
            <h2>Why Credit Suisse is battling rumours of a Lehman-style crash</h2>
              <p>
              
              Swiss bank’s plummeting share price has invited comparisons to the onset of the 2008 global financial crisis....
              </p>
              
               <div className="img"><img src={"https://www.aljazeera.com/wp-content/uploads/2022/10/000_9KW279.jpg?resize=770%2C513"} alt={kucoin} /></div> 
                <a href="https://www.aljazeera.com/economy/2022/10/5/is-credit-suisse-the-next-lehman-brothers-probably-not" target={"_blank"}><p className="text btn">read More <ArrowRightCircle size={25} /><br /></p>
              </a>

              
            </div>

            <div className="Media-news">
            <h2>Traditional Finance is Getting Richer. DeFi Can Provide a Fairer World</h2>
              <p>
              
              Traditional finance is gloating as cryptocurrencies crash. But DeFi will win in the end. It has the potential to change the world....
              </p>
              
               <div className="img"><img src={"https://s32659.pcdn.co/wp-content/uploads/2022/05/euro-on-fire-ink.jpeg.webp"} alt={kucoin} /></div> 
                <a href="https://beincrypto.com/traditional-finance-is-getting-richer-defi-can-provide-a-fairer-world/" target={"_blank"}><p className="text btn">read More <ArrowRightCircle size={25} /><br /></p>
              </a>

              
            </div>

            <div className="Media-tweet">
              <p className="text">
              <img src={tweet1} alt={tweet1} />
              </p>
              
            </div>
            
            <div className="Media-tweet">
              <p className="text">
              <img src={tweet2} alt={tweet2} />
              </p>
              
            </div>
             
            <div className="Media-video">
             
             <h2> Credit Suisse CEO Says Bank Will Rise 'Like a Phoenix'</h2>
             <p className="text">Credit Suisse shares fell as much as 12% on Monday after its new chief has asked investors for less than 100 days to deliver a new turnaround strategy. Bloomberg's Sonali Basak reports.</p>
              <iframe
                src="https://www.youtube.com/embed/CJuHCexkPf8"
                title="YouTube video player"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div> 


            <div className="Media-video">
             
             <h2> The real risk around Credit Suisse is that the rumors become self-fulfilling, says consultancy</h2>
             <p className="text">Octavio Marenzi, CEO of Opimas, says "it looks a bit like a Lehman Brothers situation."</p>
              <iframe
                src="https://www.youtube.com/embed/W-LFhXIS7Fk"
                title="YouTube video player"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>


            <div className="Media-news">
            <h2>UBS credit default swaps hit decade high after Credit Suisse blow out</h2>
              <p>
              
              The cost of insuring exposure to debt issued by Swiss lender UBS rose to a decade high on Monday after peer Credit Suisse was caug....
              </p>
              
               <div className="img"><img src={"https://www.reuters.com/resizer/st_9tSS3SfP7fu63NLnw3k2Xo8Q=/960x0/filters:quality(80)/cloudfront-us-east-2.images.arcpublishing.com/reuters/VPOFEINDOZJM3HZZTENLDQ6YMU.jpg"} alt={kucoin} /></div> 
                <a href="https://beincrypto.com/traditional-finance-is-getting-richer-defi-can-provide-a-fairer-world/" target={"_blank"}><p className="text btn">read More <ArrowRightCircle size={25} /><br /></p>
              </a>

              
            </div>
          </Col>
        </Row>
      </div>
    </Col>
  );
};
