


import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/cryptocurrency.png";
import transfer from "../assets/img/transfer.svg";
import bitcoin from "../assets/img/Bitcoin.svg"
import fail from "../assets/img/fail.svg"
import 'animate.css';
import TrackVisibility from 'react-on-screen';


export const Defi = () => {
  
  return (
    <section className="contact defi">
      <Container>
        <Row className="align-items-center defi-rows">
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={contactImg} alt="Contact Us"/>
              }
            </TrackVisibility>
          </Col>
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <h2>Why Defi ?</h2>
                <div className="defi-home">

                <p>
                Decentralised finance is an emerging industry that promises to revolutionise the traditional finance sector. The need for an open, transparent, and secure financial system is the key driver behind the decentralised finance vs. traditional finance debate, so it does not come as a surprise that decentralised finance is slowly emerging as an alternative to today’s financial system.
                </p>
                </div>
                
              </div>}
            </TrackVisibility>
          </Col>
        </Row>

        <Row className="align-items-center defi-rows">


        <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <h2>Why Defi ?</h2>
                <div className="defi-home">

                <p>
                Decentralised finance, which is a blockchain-based concept, has the potential to disrupt traditional finance because of its ability to be a financial tool that is outside of government and regulatory control.
                </p>
                </div>
                
              </div>}
            </TrackVisibility>
          </Col>


          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={transfer} alt="Contact Us"/>
              }
            </TrackVisibility>
          </Col>
          
        </Row>

        <Row className="align-items-center defi-rows">
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={bitcoin} alt="Contact Us"/>
              }
            </TrackVisibility>
          </Col>
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <h2>Why Defi ?</h2>
                <div className="defi-home ">

                <p>
                Decentralised finance threatens to phase out traditional finance because of its ability to provide financial services without geographical barriers. Traditional finance has struggled to reach some remote parts of the world, leaving billions without access to banking services. 
                </p>
                </div>
                
              </div>}
            </TrackVisibility>
          </Col>
        </Row>

        <Row className="align-items-center defi-rows">


<Col size={12} md={6}>
    <TrackVisibility>
      {({ isVisible }) =>
        <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
        <h2>Why Defi ?</h2>
        <div className="defi-home">

        <p>
        Decentralised finance seeks to revolutionise the financial sector by acting as an alternative to centrally-governed institutions, such as banks, that have historically acted as financial intermediaries.
        </p>
        </div>
        
      </div>}
    </TrackVisibility>
  </Col>


  <Col size={12} md={6}>
    <TrackVisibility>
      {({ isVisible }) =>
        <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={fail} alt="Contact Us"/>
      }
    </TrackVisibility>
  </Col>
  
</Row>



      </Container>
    </section>
  )
}





